.body-another-thing{
  -webkit-text-size-adjust: none;
  background-color: #1e1b1b;
  left: 0;
  right: 0;
  //&.body-modal{
  //  position: fixed;
  //  //top: 0;
  //  //bottom: 0;
  //  left: 0;
  //  right: 0;
  //}
}
.section-at{
  position: relative;
  width: 100%;
  //
  &__main-landing{
    position: absolute;
    top: -406px;
    width: 100%;
    height: 143%;
    left: 0;
    background-image: url(../img/dd-landing-fon.svg);
    background-repeat: no-repeat;
    background-size: 120%;
    background-position: 34% 69%;

    @media screen and (max-width: 1350px){
      top: -243px;
      left: 60px;
      height: 100%;
    }
    @media screen and (max-width: 1130px){
      background-size: 119%;
      background-position: 0;
      top: -269px;
      height: 100%;
      left: 45px;
    }
    @media screen and (max-width: 860px){
      top: -259px;
    }
    @media screen and (max-width: 725px){
      display: none;
    }
  }

  &__header{
    background-color: #1e1b1b;
    &.header{
      border: none;
      padding: 0;
    }
  }
  &__main{
    background-color: #1e1b1b;
  }
  &__blind{
    background-color: #252527;
  }
}
.page-wrap{
  &__another-thing{
    background-color: #1e1b1b;
    display: flex;
    flex-direction: column;
    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    user-select: none;
    img{
      color:#fff;
    }
  }
}

.footer-at{
  position: relative;
  background-color: #252527;
  margin-top: auto;
  &__logo-wr{
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: 394px;
    @media screen and (max-width: 585px){
      max-width: 295px;
    }
    @media screen and (max-width: 660px){
      margin-left: unset;
      order: -1;
    }
    @media screen and (max-width: 450px){
      max-width: 240px;
    }
    .at-header__cross{
      @media screen and (max-width: 767px){
        font-size: 11px;
      }
    }
    .at-header__logo {
      @media screen and (max-width: 580px){
        max-width: 70px;
      }

    }
  }
  &__logo_company{
    max-width: 176px;
    @media screen and (max-width: 767px){
      max-width: 65px;
    }
  }
  &__top {
    align-items: center;
    display: flex;
    //justify-content: space-between;
    @media screen and (max-width: 660px){
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__contacts{
    @media screen and (max-width: 480px){
      margin-bottom: 32px;
    }
    a{
      font-size: 32px;
      line-height: 32px;
      color: #FFFFFF;
      font-family: $ff-semi-bold;
      @media screen and (max-width: 767px){
        font-size: 20px;
        line-height: 24px;
      }
    }
  }
  &__soc {
    display: flex;
    margin: 0 -0.5rem;
  }
  &__soc a {
    margin: 0 0.5rem;
    display: flex;
    align-items: center;
    svg{
      fill: #686D82;
    }
  }
}
.at-header{
  &__row{
    display: flex;
    position: relative;
    //z-index: 10;

    //display: grid;
    //grid-template-columns: minmax(230px,250px) minmax(460px,600px) minmax(50px,250px);
    //grid-column-gap: 15px;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 840px){
      grid-template-columns: minmax(230px,250px) minmax(50px,250px);
    }
    @media screen and (max-width: 470px){
      grid-template-columns: minmax(50px,134px) minmax(27px,50px );
    }

  }
  &__wr-logo{
    flex-shrink: 0;
    position: relative;
    z-index: 2;
    width: 100%;
    max-width: 385px;
    min-width: 290px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media screen and (max-width: 585px){
      max-width: 284px;
      min-width: 210px;
    }
    @media screen and (max-width: 450px){
      max-width: 219px;
      min-width: 210px;
    }
    @media screen and (max-width: 350px){
      min-width: 190px;
    }

  }
  &__logo{
    max-width: 95px;
    //min-width: 95px;
    @media screen and (max-width: 585px){
      max-width: 75px;
      min-width: 75px;
    }
    @media screen and (max-width:450px){
      max-width: 60px;
      min-width: 60px;
    }


  }

  &__cross{
    font-size: 16px;
    line-height: 20px;
    font-family: $ff-semi-bold;
    color:#fff;
    @media screen and (max-width: 470px){
      font-size: 9px;
      line-height: 11px;
    }
  }
  &__logo_min{
    max-width: 90px;
    //min-width: 90px;
    margin: 0 10px;
    @media screen and (max-width: 585px){
      max-width: 90px;
    }
    @media screen and (max-width: 450px){
      max-width: 60px;
    }
  }
  &__logo_company{
    max-width: 176px;
    //min-width: 176px;
    padding-top: 3px;
    @media screen and (max-width: 585px){
      max-width: 120px;
      min-width: 120px;
    }
    @media screen and (max-width: 450px){
      max-width: 90px;
      min-width: 90px;
    }

  }
  &__buttons__connection{
    height: 3rem;
    left: calc(50% + 39.375rem);
    //opacity: 0;
    position: absolute;
    //visibility: hidden;
    top: 1.25rem;
    transition: opacity .3s;
    width: 3rem;
    z-index: 12;
    //@media screen and (max-width: 1504px){
    //  left: auto;
    //  right: 4.125rem;
    //  bottom: 1.313rem;
    //}
  }
  &__menu{
    position: relative;
    z-index: 2;
    margin: 0 15px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 600px;
    min-width: 485px;
    @media screen and (max-width: 1011px){
      display: none;
    }

    li {
      margin: 0 5px;
      cursor:pointer;
      font-size: 16px;
      line-height: 24px;
      color: #fff;
      font-family: $ff-semi-bold;

      &:hover {
        color: #A3D562;
      }
    }
  }
  &__container-icon{
    width: 100%;
    max-width: 385px;
    min-width: 50px;
    display: flex;
    justify-content: flex-end;
    position: relative;
    @media screen and (max-width: 470px){
      position: unset;
    }
  }
  &__wr-icon{
    //width: 100%;
    //max-width: 250px;
    //min-width: 50px;
    position: relative;
    z-index: 11;
    display: flex;
    align-items: center;
    //justify-content: flex-end;
    @media screen and (max-width: 970px){
      //display: none;
    }
  }
  &__wr-icon-item{
    cursor: pointer;
  }
  &__private-office{
    cursor:pointer;
    //margin-left: auto;
  }

  &__wr-logo-media{
    display: flex;
    align-items: center;
    .at-header__cross{
      margin: 0 15px;
    }
  }
  &__menu-media{
    display: flex;
    flex-direction: column;
    min-height: 100%;
  }
  &__menu-media-content{
    flex: 1 0 auto;
  }
  &__menu-header{

  }

  &__menu-list-media{
    li {
      &:not(:first-child) {
        margin-top: 20px;
      }
      a {
        font-size: 38px;
        line-height: 44px;
        color: #fff;
        font-family: $ff-medium;
        &:hover{
          color:#A3D562;
        }
        @media screen and (max-width: 580px){
          font-size: 16px;
          line-height: 24px;
          margin-top: 16px;
        }
      }


    }
  }
  &__menu-media-footer{
    display: flex;
    align-items: center;
    svg{
      margin-right: 12px;
    }
    p{
      font-size: 16px;
      line-height: 24px;
      color:#fff;
      font-family: $ff-medium;
    }
  }
  &__wr-media-footer{
    flex: 0 0 auto;
    border-top: 1px solid #464854;
  }
  &__menu-media-close{
    position: absolute;
    right: 0;
    top: 0;
  }
  &__add-element{
    position: fixed;
    right: 0;
    top: 0;
    z-index: 2000;
  }
  &__connection{
    position: absolute;
    right: 81px;
    top: 30px;
    @media screen and (max-width: 767px){
      top: 37px;
    }
    @media screen and (max-width: 470px){
      top: 26px;
    }
    &.header-buttons__connection{
      opacity: 1;
      visibility: visible;
      left: unset;
    }
    &.un-active{
      display: none;
    }
  }
  &__btn-menu{
    cursor:pointer;
    position: absolute;
    right: 49px;
    top: 45px;
    z-index: 20000;
    @media screen and (max-width: 470px){
      top: 34px;
    }
    &.un-active{
      display: none;
      @media screen and (max-width: 970px){
        display: block;
      }
    }
    rect{
      transition: transform 0.25s;
    }
    &.active{
      display: block;
      rect{
        &:first-child{
          transform: rotate(30deg);
          transform-origin: 5px 3px;
        }
        &:last-child{
          transform: rotate(-30deg);
          transform-origin: 5px 3px;
        }
      }
    }
  }


}

.another-thing{
  &__h1-subtitle-img{
    margin-top: 10px;
    width: 100%;
    max-width: 370px;
    margin-right: 17px;
    @media screen and (max-width: 860px){
      margin-top: 0;
      max-width: 314px;
    }
    @media screen and (max-width:725px){
      margin-right: 0;
      margin-top: 3px;
      max-width: 373px;
    }
    @media screen and (max-width:480px){
      margin-right: 0;
      margin-top: 3px;
      max-width: 265px;
    }
    img{
      &:first-child{
        @media screen and (max-width:725px){
          display: none;
        }
      }
      &:last-child{
        display: none;
        @media screen and (max-width:725px){
          width: 100%;
          display: block;
        }
      }
    }
  }
  &__title-row{
    display: grid;
    grid-template-columns:minmax(540px,610px) minmax(290px, 1fr);
    @media screen and (max-width: 860px){
      grid-template-columns: minmax(200px,450px) minmax(290px,1fr);
    }
    @media screen and (max-width: 725px){
      grid-template-columns: 1fr;
    }

  }
  &__wr-title{
    //
    width: 100%;
    max-width: 600px;
    @media screen and (max-width: 725px){
      display: flex;
      flex-direction: column;
      align-items: center;
      max-width: 100%;
    }

  }
  &__h1{
    font-size: 64px;
    line-height: 64px;
    font-family: $ff-semi-bold;
    letter-spacing: -0.015em;
    color: #FFFFFF;
    @media screen and (max-width: 860px){
      font-size: 48px;
      line-height: 48px;
    }
    @media screen and (max-width: 725px){
      text-align: center;
    }
    @media screen and (max-width: 480px){
      font-size: 32px;
      line-height: 32px;
    }


  }
  &__h1-subtitle{
    display: flex;
    align-items: center;
    //width: 100%;
    //max-width: 543px;
    //justify-content: space-between;
    @media screen and (max-width: 725px){
      flex-direction: column;
      width: 100%;
    }
    p:first-child{
      font-size: 64px;
      line-height: 64px;
      font-family: $ff-semi-bold;
      letter-spacing: -0.015em;
      color: #A3D562;
      @media screen and (max-width: 860px){
        font-size: 48px;
        line-height: 56px;
      }
      @media screen and (max-width: 480px){
        font-size: 32px;
        line-height: 32px;
      }
    }
    p:last-child{
      padding-top: 10px;
      font-size: 14px;
      line-height: 18px;
      font-family: $ff-medium;
      color: #FFFFFF;
      max-width: 90px;
      margin-left: 10px;
      @media screen and (max-width: 860px){
        padding-top: 0;
      }
      @media screen and (max-width: 725px){
        max-width: 100%;
        margin-top: 12px;
        padding-top: 0;
      }

    }


  }
  &__title-img{
    picture{
      position: relative;
      z-index: 2;
    }
    top: -32px;
    left: 112px;
    position: relative;
    width: 100%;
    max-width: 541px;
    @media screen and (max-width: 1310px){
      left: 0;
    }
    @media screen and (max-width: 770px){
      top: 15px;
    }
    @media screen and (max-width: 725px){
      display: none;
    }

  }
  &__title-img-ellipse{
    position: absolute;
    top: 56px;
    right: -105px;
    z-index: 1;
    @media screen and (max-width: 1480px){
      right: -14px;
    }
    @media screen and (max-width: 850px){
      top: -27px;
      //right: -97px;
    }
  }
  &__advantage{
    margin-top: -5px;
    width: 100%;
    max-width: 820px;
    @media screen and (max-width: 850px){
      max-width: 100%;
    }
    .swiper-slide{
      min-width: 205px;
      @media screen and (max-width: 850px){
        width: 30%;
        min-width: 225px;
        &:last-child {
          padding-right: 16px;
        }
      }
      @media screen and (max-width: 470px){
        max-width: 154px;

      }
    }
  }
  &__advantage-img{
    height: 72px;
    display: flex;
    align-items: center;
    @media screen and (max-width: 780px){
      height: 48px;
    }
    @media screen and (max-width: 470px){
      height: 32px;
    }
    img{
      max-height: 100%;
      width: auto;
    }
  }
  &__advantage-title{
    margin-top: 24px;
    font-size: 24px;
    line-height: 26px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;
    @media screen and (max-width: 780px){
      font-size: 20px;
      line-height: 22px;
      margin-top: 20px;
    }
    @media screen and (max-width: 470px){
      font-size: 16px;
      line-height: 20px;
    }

  }
  &__advantage-des{
    margin-top: 12px;
    font-size: 16px;
    line-height: 24px;
    font-family: $ff-semi-bold;
    color: #737373;
    @media screen and (max-width: 780px){
      font-size: 16px;
      line-height: 20px;
    }
    @media screen and (max-width: 470px){
      font-size: 14px;
      line-height: 18px;
      margin-top: 8px;
    }
  }
  &__wr-part{
    position: relative;
    margin-top: 46px;
    margin-bottom: 90px;
    display: flex;
    align-items: center;
    @media screen and (max-width: 780px){
      margin-top: 40px;
    }

    @media screen and (max-width: 580px){
      margin-bottom: 56px;
    }
    @media screen and (max-width: 480px){
      margin-top: 44px;
    }
    @media screen and (max-width: 360px){
      flex-direction: column;
      width: 100%;
    }
    .another-thing__alert{
      left: 169px;
      top: 0;
      transform: translateY(-59%);
      @media screen and (max-width: 725px){
        left: 50%;
        top: -100%;
        transform: translate(-50%, -63%);
      }
      @media screen and (max-width: 360px){
        transform: translate(-50%, -35%);
      }
    }
  }
  &__alert{
    display: none;
    position: absolute;
    z-index: 20;
    background-color: #fff;
    border-radius: 4px;
    padding: 15px;
    width: 100%;
    max-width: 331px;
    box-shadow: 0 0 11px 0 rgba(255, 255, 255, 0.64);
  }
  &__alert-close{
    cursor:pointer;
    position: absolute;
    right: 6px;
    top: 6px;
  }
  &__part-btn{
    position:relative;
    z-index: 10;
    cursor:pointer;
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-medium;
    margin-right: 24px;
    background-color: #A3D562;
    border-radius: 60px;
    padding: 20px 24px;
    border:1px solid #A3D562;
    text-align: center;
    color: #1C1B28;
    @media screen and (max-width: 780px){
      padding: 9px 38px;
      font-size: 16px;
      line-height: 24px;
    }
    &:hover{
      background-color:transparent;
      color:#fff;
    }
    @media screen and (max-width: 360px){
      //width: 100%;
      margin-right: 0;
      font-size: 16px;
      line-height: 20px;
    }
  }
  &__participation-rules{
    font-size: 18px;
    line-height: 24px;
    font-family: $ff-semi-bold;
    color: #FFFFFF;
    &:hover{
      color: #ecebeb;
    }
    @media screen and (max-width: 780px){
      font-size: 16px;
      line-height: 24px;
      font-family: $ff-medium;
    }
    @media screen and (max-width: 360px){
      margin-top: 20px;
    }
  }
  &__row-des{
    display: grid;
    grid-template-columns: minmax(300px,1007px) minmax(240px,1fr);
    grid-column-gap: 60px;
    @media screen and (max-width: 770px){
      grid-template-columns: 1fr;
    }
    grid-row-gap: 40px;
  }
  &__title-h2{
    font-size: 48px;
    line-height: 56px;
    color: #FFFFFF;
    font-family: $ff-medium;
    background-color: #1e1b1b;
    position: relative;
    z-index: 1;
    //@media screen and (max-width: 1130px){
    //  font-size: 33px;
    //  line-height: 39px;
    //}
    @media screen and (max-width: 770px){
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -0.015em;
      font-family: $ff-semi-bold;
    }
    @media screen and (max-width: 470px){
      font-size: 20px;
      line-height: 22px;
    }

  }
  &__des-item-expl{
    position: relative;
    margin-top: 18px;
  }
  &__text-circle{
    max-width: 328px;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(28%,-50%) rotate(34deg);
    @media screen and (max-width: 1220px){
      transform: translate(14%,-50%) rotate(34deg);
    }
    @media screen and (max-width: 1130px){
      transform: translate(-19%,-53%) rotate(51deg);
    }
    @media screen and (max-width: 915px){
      transform: translate(-8%,-54%) rotate(49deg);
      width: 188px;
    }
    @media screen and (max-width: 884px){
      transform: translate(31%,-54%) rotate(49deg);
      width: 188px;
    }

    @media screen and (max-width: 770px){
      width: 148px;
      transform: translate(14%,-51%) rotate(54deg);
    }
    @media screen and (max-width: 500px){
      display: none;
    }


  }
  &__wr-des-title{
    position: relative;
    //padding: 30px 0;
    background-color: #1e1b1b;
    z-index: 2;
    width: 100%;
    max-width: 824px;

    @media screen and (max-width: 1130px){
      max-width: 600px;
    }
    @media screen and (max-width: 884px){
      max-width: 450px;
    }
    @media screen and (max-width: 770px){
      //max-width: 96%;
    }
    @media screen and (max-width: 700px){
      //max-width: 82%;
    }
    @media screen and (max-width: 646px){
      //max-width: 91%;
    }
    @media screen and (max-width: 475px){
      max-width: 100%;
    }

    .another-thing__title-h2{
      padding: 17px 0;
      letter-spacing: -0.015em;
      font-size: 48px;
      line-height: 48px;
      @media screen and (max-width: 1130px){
        //padding: 7px 0;
        padding: 0;
        font-size: 33px;
        line-height: 39px;
        width: 100%;
        max-width: 520px;
      }
      @media screen and (max-width: 885px){
        font-size: 24px;
        line-height: 24px;
      }
      @media screen and (max-width: 392px){
        font-size: 23px;
        line-height: 24px;
      }

    }




  }
  &__des-explanation{
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;
    width: 100%;
    max-width: 600px;
    margin-top: 27px;

    @media (min-width: 470px) and (max-width: 1130px) {
      max-width: 376px;
    }
    @media (min-width: 470px) and (max-width: 1020px) {
      max-width: 400px;
    }
    @media screen and (max-width: 780px){
      font-size: 16px;
      line-height: 20px;
      font-family: $ff-medium;
      letter-spacing: -0.015em;
      margin-top: 32px;
      max-width: 380px;
    }
    @media screen and (max-width: 475px){
      max-width: 100%;
      margin-top: 28px;
    }



  }
  //&__des-list{
  //  display: grid;
  //  grid-template-columns: repeat(2,1fr);
  //  grid-column-gap: 16px;
  //  width: 100%;
  //  max-width: 604px;
  //  margin-top: 15px;
  //  @media screen and (max-width:570px){
  //    grid-template-columns: 1fr;
  //  }
  //}
  &__des-list-col{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-column-gap: 16px;
    width: 100%;
    max-width: 604px;
    margin-top: 15px;
    @media screen and (max-width:570px){
      grid-template-columns: 1fr;
    }
  }
  &__des-list-item{
