$add-zone-padding: 59px;
.test-cont{
  //margin-bottom: 32px;
}
.container-for-test{
  width: 100%;
  max-width: 812px;
  margin: 0 auto;
  padding: 40px 48px 48px;
  background: linear-gradient(30.77deg, #2F282E 8.83%, #2A2A33 32.56%, #2E4654 76.33%);
  border-radius: 20px;
  @media screen and (max-width: 570px){
    padding: 28px;
  }
}
.test-n-multiple-choice{
  &.test-a__multiple-choice{
    .test-n-multiple-choice__row{
      border: none;
      padding: 12px 40px 12px 24px;
      border-radius: 8px;
      @media screen and (max-width: 767px){
        flex-direction: row;
      }
      &:nth-child(even){
        background-color: #6B7081;
      }
    }
  }

}
.test-a{
  &__main-container{
    position:relative;
    .swiper-wrapper{
      will-change:height,transform;
      //transition-duration:100ms!important;
      transition-timing-function:ease-in-out!important;
    }
  }
  &__container{
    position:relative;
  }
  &__text-center{
    p{
      text-align: center;
      @media screen and (max-width: 620px){
        text-align: left;
      }
    }
  }
  &__body{
    .test-n-multiple-choice__row_main{
      //padding-bottom: 9px!important;
    }
    .test-n-multiple-choice__row{
      border: none;
      border-radius: 8px;
      padding-right: 42px;
      padding-top: 11px;
      padding-bottom: 13px;
      padding-left: 23px;
      align-items: center;
      @media screen and (max-width: 767px){
        flex-direction: row;
      }
      @media screen and (max-width: 630px){
        flex-direction:column;
        align-items: flex-start;
      }
      @media screen and (max-width: 470px){
        padding: 20px 16px;
        align-items: flex-start;
      }
      &.test-n-multiple-choice__row_main{
        padding-bottom: 9px;
      }
      &:nth-child(even){
        background-color: #6B7081;
      }
    }
    .test-n-multiple-choice__elections{
      @media screen and (max-width: 767px){
        display: flex;
        gap: 20px;
        margin-left: auto;
        margin-top: 0;
      }
      @media screen and (max-width: 630px){
        margin-top: 20px;
        margin-left: 0;
      }
    }
    .test-n-checkbox__icon{
      width: 16px;
      height: 16px;
      box-sizing: border-box;
    }
  }
  &__add-zone-status-moderation{
    .test-a__add-zone-wr-btn{
      margin-top: 40px;
      svg{
        flex-shrink: 0;
      }
      @media screen and (max-width: 470px){
        flex-direction: row;
      }
    }
  }
  &__slider-btn{
    margin-top: 32px;
    display: flex;
    align-items: center;
    .test-a__btn{
      padding: 10px 23px;
      @media screen and (max-width: 470px){
        padding: 6px 14px;
        //&:hover{
        //  background-color: unset;
        //  color: unset;
        //}
      }
    }
    .test-a__btn.white{
      border: 2px solid #CDD6E3;
    }

  }
  &__container-finansial-add{
    .swiper-container{
      @media screen and (max-width: 470px){
        display: flex;
        align-items: center;
      }
    }
  }
  &__container-finansial-add{
    .test-a__slider-btn-opacity{
      opacity: 0;
    }
  }
  &__slider-btn-opacity{
    margin-top: 0;
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    .test-a__btn{
      padding: 10px 23px;
      @media screen and (max-width: 470px){
        padding: 6px 14px;
      }
    }

    @media screen and (max-width: 470px){
      //opacity: 1;
      //z-index: 10;
    }
  }
  &__btn{
    width: max-content;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    padding: 8px 20px;
    font-size: 16px;
    line-height: 24px;
    color: #1C1B28;
    font-family: $ff-semi-bold;
    transition: background-color .3s,border-color .3s,color .3s;
    border: 2px solid transparent;
    &.white{
      background-color: #FFFFFF;
      border-color: #fff;
      &.swiper-button-disabled{
        display: none;
      }
    }
    &.green{
      background-color:  #A3D562;
      border-color: #A3D562;
    }
    &.unactive{
      background-color: #F1F2F4;
      border-color: #F1F2F4;
      color: #BEC6D1;
      pointer-events: none;
    }
    &:not(:last-child){
      margin-right: 16px;
    }
    &:hover{
      background-color:transparent;
      color:#fff;
      p{
        color:#fff;
      }
    }
    //&.swiper-button-disabled{
    //  display: none;
    //}
    @media screen and (max-width: 570px){
      font-size: 16px;
      line-height: 20px;
    }

