  &__counter{
    margin-bottom: 8px;
  }
  &__counter-des{
    color: #A2AEBF;
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
  }

  &__question{
    //margin-top: 8px;
    margin-bottom: 24px;
    font-size: 24px;
    line-height: 26px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;
    @media screen and (max-width: 570px){
      font-size: 20px;
      line-height: 22px;
    }
    p{
      font-size: 24px;
      line-height: 26px;
      color: #FFFFFF;
      font-family: $ff-semi-bold;
      @media screen and (max-width: 570px){
        font-size: 20px;
        line-height: 22px;
      }
    }

  }
  &__final-window-question{
    margin-bottom: 8px;
  }
  &__footer{
    .test-n-dual__button{
      cursor: pointer;
      padding: 10px 10px;
      justify-content: center;
      transition: background-color .3s,border-color .3s,color;
      align-items: center;
      border: 1px solid #8bc540;
      border-radius: 40px;
      display: inline-flex;
      font-family: Gilroy-Regular,sans-serif;
      font-size: 1.25rem;
    }
  }
  &__images-row{
    width: 100%;
    max-width: 518px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
    @media screen and (max-width: 470px){
      grid-template-columns: repeat(1,1fr);
      width: calc(100% - 2px);
    }
    .test-n-images__item{
      width: auto;
      &:last-child{
        @media screen and (max-width: 470px){
          margin-bottom: 32px;
        }
      }
      .test-n-images__image{

        &.test-n-images__image_answered{
          &:before{
            z-index: 10;
          }
        }
        @media screen and (max-width: 780px){
          &:hover{
            &:after{
              //border-color: transparent;
            }

          }
        }

        &:after{
          box-sizing: border-box;

        }

        img{
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          aspect-ratio:unset;
          object-fit: cover;
        }
      }
    }
  }
  &__image-container{
    position: relative;
    &:before{
      content: "";
      display: block;
      padding-top: 60%;
    }
  }
  &__slider {
    &.test-n-slider {
      padding-left: 20px;
