.test-n {
  background-color: $color-bg-black;
  border-radius: 1.25rem;
  margin: 0 auto;
  max-width: 57.25rem;
  padding: 3.5rem;
  position: relative;

  @include respondTo(mobile) {
    padding: 2rem 1.75rem 2.5rem;
  }

  &_white {
    background-color: $color-bg-white;
  }

  &_gray {
    background-color: $color-bg-gray;
  }
  &__area-final-window-des{
    border-top: 1px solid #caccdf;
    width: 100%;
    margin-top: 40px;
    padding-top: 40px;

    color: $color-text-white;
    font-size: 17px;
    line-height: 1.4;
    font-family: $ff-medium;
    text-align: center;
    a{
      color: #8bc540;
      &:hover{
        color: #84bb3d;
      }
    }
    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
    p {
      color: $color-text-white;
      font-size: 17px;
      line-height: 1.4;
      font-family: $ff-medium;
      text-align: center;
      a{
        color: #8bc540;
        &:hover{
          color: #84bb3d;
        }
      }
      .test-n_white &, .test-n_gray & {
        color: $color-text;
      }
    }
  }
  &__area-question-loader{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% + 7rem);
    height: calc(100% + 7rem);;
    z-index: 11;
    background-color: inherit;
    //&_white {
    //  background-color: $color-bg-white;
    //}
    //
    //&_gray {
    //  background-color: $color-bg-gray;
    //}
  }

  &__title {
    color: $color-text-white;
    display: block;
    font-family: $ff-semi-bold;
    font-size: 1.5rem;
    line-height: 1.08;

    @include respondTo(mobile) {
      font-size: 1.25rem;
      line-height: 1.1;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
    &.center{
      text-align: center;
    }
  }
  &__correct_answers{
    color: $color-text-white;
    display: block;
    font-family: $ff-semi-bold;
    font-size: 20px;
    line-height: 1.08;
    margin-top: 20px;

    @include respondTo(mobile) {
      font-size: 1.25rem;
      line-height: 1.1;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
    &.center{
      text-align: center;
    }
  }

  &__description {
    color: $color-text-white;
    display: block;
    font-size: 1.25rem;
    line-height: 1.4;
    margin-top: 1rem;

    @include respondTo(mobile) {
      font-size: 1rem;
      letter-spacing: -0.015em;
      line-height: 1.25;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }

    p {
      color: $color-text-white;
      font-size: 1.25rem;
      line-height: 1.4;

      @include respondTo(mobile) {
        font-size: 1rem;
        letter-spacing: -0.015em;
        line-height: 1.25;
      }

      .test-n_white &, .test-n_gray & {
        color: $color-text;
      }
    }
  }

  &__answer {
    border-top: 1px solid rgba(202, 204, 223, 0.5);
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    margin-bottom: 2rem;
    padding-top: 2.5rem;

    @include respondTo(mobile) {
      margin-bottom: 1.5rem;
      padding-top: 2rem;
    }
  }

  &__answer-title {
    color: $color-text-white;
    display: block;
    font-family: $ff-semi-bold;
    font-size: 1.5rem;
    line-height: 1.08;

    @include respondTo(mobile) {
      font-size: 1.25rem;
      letter-spacing: -0.015em;
      line-height: 1.1;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
  }

  &__answer-body {
    color: $color-text-white;
    display: block;
    font-family: $ff-medium;
    font-size: 1.25rem;
    line-height: 1.4;

    @include respondTo(mobile) {
      font-size: 1rem;
      letter-spacing: -0.015em;
      line-height: 1.25;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }

    p {
      color: $color-text-white;
      font-family: $ff-medium;
      font-size: 1.25rem;
      line-height: 1.4;

      @include respondTo(mobile) {
        font-size: 1rem;
        letter-spacing: -0.015em;
        line-height: 1.25;
      }

      .test-n_white &, .test-n_gray & {
        color: $color-text;
      }
    }

    a {
      color: $color-card-green;
    }
  }

  &__footer {
    margin-top: 2.5rem;

    @include respondTo(mobile) {
      margin-top: 2rem;
    }
  }

  &__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;

    //@include respondTo(mobile) {
    //  align-items: flex-start;
    //  flex-direction: column;
    //}
  }
  &__area-question{
    position:relative;
  }

  &__area-final-window{
    position: relative;
    min-height: 240px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__emoticon{
    max-width: 3rem;
  }
}

.test-n-wrap {

  &:not(:last-of-type) {
		margin-bottom: 2rem;

		@include respondTo(mobile) {
			margin-bottom: 1.5rem;
		}
	}
}

@mixin position-center($text-align: center) {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  text-align: $text-align;
}

.loading-text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  width: 100%;
  height: 100px;
  line-height: 100px;
}
.loading-text span {
  display: inline-block;
  margin: 0 5px;
  color: #8bc540;
  font-family: "Quattrocento Sans", sans-serif;
}
.loading-text span:nth-child(1) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 0s infinite linear alternate;
  animation: blur-text 1.5s 0s infinite linear alternate;
}
.loading-text span:nth-child(2) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 0.2s infinite linear alternate;
  animation: blur-text 1.5s 0.2s infinite linear alternate;
}
.loading-text span:nth-child(3) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 0.4s infinite linear alternate;
  animation: blur-text 1.5s 0.4s infinite linear alternate;
}
.loading-text span:nth-child(4) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 0.6s infinite linear alternate;
  animation: blur-text 1.5s 0.6s infinite linear alternate;
}
.loading-text span:nth-child(5) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 0.8s infinite linear alternate;
  animation: blur-text 1.5s 0.8s infinite linear alternate;
}
.loading-text span:nth-child(6) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 1s infinite linear alternate;
  animation: blur-text 1.5s 1s infinite linear alternate;
}
.loading-text span:nth-child(7) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 1.2s infinite linear alternate;
  animation: blur-text 1.5s 1.2s infinite linear alternate;
}
.loading-text span:nth-child(8) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 1.4s infinite linear alternate;
  animation: blur-text 1.5s 1.4s infinite linear alternate;
}
.loading-text span:nth-child(9) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 1.6s infinite linear alternate;
  animation: blur-text 1.5s 1.6s infinite linear alternate;
}
.loading-text span:nth-child(10) {
  filter: blur(0px);
  -webkit-animation: blur-text 1.5s 1.8s infinite linear alternate;
  animation: blur-text 1.5s 1.8s infinite linear alternate;
}

@-webkit-keyframes blur-text {
  0% {
    filter: blur(0px);
  }
  100% {
    filter: blur(4px);
  }
}

@keyframes blur-text {
  0% {
    filter: blur(0px);
  }
  100% {
    filter: blur(4px);
  }
}

.circle {
  position: absolute;
  border: 3px solid transparent;
  border-top-color: #9ee925;
  border-radius: 50%;
  -webkit-animation: rotate linear infinite;
  animation: rotate linear infinite;
}
.circle.one {
  height: 50px;
  width: 50px;
  left: 50px;
  top: 50px;
  -webkit-animation-duration: 0.85s;
  animation-duration: 0.85s;
}
.circle.two {
  height: 75px;
  width: 75px;
  top: 38px;
  left: 38px;
  -webkit-animation-duration: 0.95s;
  animation-duration: 0.95s;
}
.circle.three {
  height: 100px;
  width: 100px;
  top: 25px;
  left: 25px;
  -webkit-animation-duration: 1.05s;
  animation-duration: 1.05s;
}
.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -90px 0 0 -90px;
}

@-webkit-keyframes rotate {
  from {
    transform: rotateZ(360deg);
  }
  to {
    transform: rotateZ(0deg);
  }
}
@keyframes rotate {
  from {
    transform: rotateZ(360deg);
  }
  to {
    transform: rotateZ(0deg);
  }
}
