      .personal-office__notifications-icon {
        background-color: #FB9C0F;
        background-image: url("data:image/svg+xml,%3Csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.5 2.20508V0.46875C7.5 0.210938 7.28906 0 7.03125 0H2.96875C2.71094 0 2.5 0.210938 2.5 0.46875V2.20508C1.73242 2.89062 1.25 3.88867 1.25 5C1.25 6.11133 1.73242 7.10938 2.5 7.79492V9.53125C2.5 9.78906 2.71094 10 2.96875 10H7.03125C7.28906 10 7.5 9.78906 7.5 9.53125V7.79492C8.26758 7.10938 8.75 6.11133 8.75 5C8.75 3.88867 8.26758 2.89062 7.5 2.20508ZM3.28125 0.78125H6.71875V1.66602C6.20312 1.40039 5.61914 1.25 5 1.25C4.38086 1.25 3.79688 1.40039 3.28125 1.66602V0.78125ZM6.71875 9.21875H3.28125V8.33398C3.79688 8.59961 4.38086 8.75 5 8.75C5.61914 8.75 6.20312 8.59961 6.71875 8.33398V9.21875ZM5 7.8125C3.45898 7.8125 2.1875 6.56641 2.1875 5C2.1875 3.46484 3.42773 2.1875 5 2.1875C6.54102 2.1875 7.8125 3.43359 7.8125 5C7.8125 6.53516 6.57227 7.8125 5 7.8125ZM5.74805 6.41406L4.55469 5.60156C4.49023 5.55859 4.45312 5.48633 4.45312 5.4082V3.20312C4.45312 3.07422 4.55859 2.96875 4.6875 2.96875H5.3125C5.44141 2.96875 5.54688 3.07422 5.54688 3.20312V4.95312L6.36523 5.50977C6.47266 5.58203 6.5 5.72852 6.42773 5.83594L6.07617 6.35156C6.00195 6.45898 5.85547 6.48633 5.74805 6.41406Z' fill='white'/%3E%3C/svg%3E");
      }
    }

