      .personal-office__notifications-icon {
        background-color: #686D82;
        background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='10' height='10' viewBox='0 0 10 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath fill-rule='evenodd' clip-rule='evenodd' d='M4.99992 1.25008C4.55789 1.25008 4.13397 1.42568 3.82141 1.73824C3.50885 2.0508 3.33325 2.47472 3.33325 2.91675V4.16675H6.66658V2.91675C6.66658 2.47472 6.49099 2.0508 6.17843 1.73824C5.86587 1.42568 5.44195 1.25008 4.99992 1.25008ZM7.49992 4.16675V2.91675C7.49992 2.25371 7.23653 1.61782 6.76769 1.14898C6.29884 0.68014 5.66296 0.416748 4.99992 0.416748C4.33688 0.416748 3.70099 0.68014 3.23215 1.14898C2.76331 1.61782 2.49992 2.25371 2.49992 2.91675V4.16675H2.08325C1.3929 4.16675 0.833252 4.72639 0.833252 5.41675V8.33342C0.833252 9.02377 1.3929 9.58342 2.08325 9.58342H7.91658C8.60694 9.58342 9.16658 9.02377 9.16658 8.33342V5.41675C9.16658 4.72639 8.60694 4.16675 7.91658 4.16675H7.49992ZM2.08325 5.00008C1.85313 5.00008 1.66659 5.18663 1.66659 5.41675V8.33342C1.66659 8.56353 1.85313 8.75008 2.08325 8.75008H7.91658C8.1467 8.75008 8.33325 8.56353 8.33325 8.33342V5.41675C8.33325 5.18663 8.1467 5.00008 7.91658 5.00008H2.08325Z' fill='white'/%3e%3c/svg%3e");
      }
    }

    &.status-current {
      .personal-office__notifications-icon {
        opacity: 0;
      }
    }
  }

  &__notifications-list-non{
    padding: 12px 20px;
    background-color: #313137;
    p{
      color:#fff;
      font-size: 16px;
      line-height: 20px;
      font-family: $ff-medium;
    }

  }
  &__notifications-list {
    max-height: 440px;
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-color: #fff #caccdf;
    scrollbar-width: thin;
    @media screen and (max-width: 470px) {
      max-height: calc(100vh - 67px);

    }

    &::-webkit-scrollbar {
      width: 4px;
      height: 4px;
    }

    &::-webkit-scrollbar-button {
      width: 0;
      height: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: #fff;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-track {
      background: #686D82;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-corner {
      background: transparent;
    }

    li {
      cursor: pointer;
      padding: 12px 10px 12px 24px;

      &:hover {
        background-color: #313137;
      }

      &:not(:first-child) {
        margin-top: 4px;
      }


    }
  }

  //
  &__notifications-list-item {
    position: relative;
    display: block;
    padding: 12px 24px 12px 24px;

    &[data-link="true"] {
      cursor: pointer;

      &:hover {
        background-color: #313137;
      }
    }

    &[data-read="false"] {
      background-color: #313137;
    }

    &:not(:first-child) {
      margin-top: 4px;
    }

    &.status-checked {
      .personal-office__notifications-icon {
        background-color: #82BF00;
        background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7.62796 0.205535C7.79068 0.368253 7.79068 0.632072 7.62796 0.794791L3.04463 5.37812C2.88191 5.54084 2.61809 5.54084 2.45537 5.37812L0.372039 3.29479C0.20932 3.13207 0.20932 2.86825 0.372039 2.70553C0.534757 2.54282 0.798576 2.54282 0.961294 2.70553L2.75 4.49424L7.03871 0.205535C7.20142 0.0428165 7.46524 0.0428165 7.62796 0.205535Z' fill='white'/%3E%3C/svg%3E");
      }
