  }
  &__notifications-list-wr-link{
    position: absolute;
    width: 99%;
    height: 99%;
    left: 0;
    top: 0;
  }

  &__notifications-wr-not {
    margin-left: 12px;
  }

  &__notifications-info {
    display: flex;
  }

  &__notifications-icon {
    margin-top: 2px;
    width: 18px;
    height: 18px;
    flex-shrink: 0;
    border-radius: 50%;
    background-position: center;
    background-repeat: no-repeat;
  }

  &__notifications-title {
    font-size: 16px;
    line-height: 24px;
    color: #FFFFFF;
    font-family: $ff-semi-bold;

  }

  &__notifications-data {
    margin-top: 4px;
    font-size: 14px;
    line-height: 18px;
    color: #686D82;
    font-family: $ff-medium;
  }

  &__empty-container {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparent;
    z-index: 10;

    &.active {
      display: block;
    }
  }

  &__notifications-close {
    cursor: pointer;
  }

  //  

  &__flex-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__h2-title {
    font-size: 24px;
    line-height: 26px;
    font-family: $ff-semi-bold;
    color: #fff;
    @media screen and (max-width: 580px) {
      font-size: 20px;
      line-height: 24px;
    }
  }

  &__container-tasks {
    margin-top: 32px;
  }

  &__group-tasks {
    position: relative;
    &.active {
      .personal-office__tasks-rows {
        max-height: 1000px;
      }
    }
    &:not(:first-child) {
      margin-top: 46px;

      &:before {

      }
    }
    &:not(:last-child) {
      &:after {
        content: "";
        display: block;
        position: relative;
        width: 100%;
        height: 1px;
        background-color: #464854;
        transform: translateY(23px);
      }
    }
    &.active {
      &:after {
        display: none;
      }

      .personal-office__tasks-arrow {
        transform: rotate(180deg);
      }
    }
    .personal-office__tasks-title{
      font-size: 20px;
      line-height: 28px;
      color: #FFFFFF;
      font-family: $ff-bold;
      @media screen and (max-width: 770px) {
        font-size: 16px;
        line-height: 24px;
      }
      @media screen and (max-width: 480px) {
        font-family: $ff-semi-bold;
        letter-spacing: -0.015em;
        line-height: 20px;
      }

    }

  }

  &__tasks-arrow {
    transition-duration: .3s;
  }

  &__group-wr-tasks-title {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    //min-height: 50px;
  }

  &__tasks-title {
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-bold;
    color: #fff;
    margin-right: 10px;
    @media screen and (max-width: 770px) {
      font-size: 20px;
      line-height: 24px;
      font-family: $ff-semi-bold;
    }

  }

  &__tasks-description {
    margin-top: 9px;

    p {
      font-size: 14px;
      line-height: 18px;
      color: #BEC6D1;
      font-family: $ff-medium;

      &:not(:first-child) {
        margin-top: 8px;
      }

    }

    a {
      font-size: 14px;
      line-height: 18px;
      color: #A3D562;
      font-family: $ff-medium;
    }
  }

  &__tasks-rows {
    max-height: 0;
    overflow: hidden;
    transition-duration: .5s;
    will-change: max-height;
  }

  &__tasks-rows-wr {
    margin-top: 24px;
    margin-bottom: 9px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
    @media (min-width: 815px) and (max-width: 1020px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 740px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 530px) {
      grid-template-columns: repeat(1, 1fr);
    }


  }

  &__tasks {
    overflow: hidden;
    position: relative;
    cursor: pointer;
    background-color: #313137;
    border-radius: 12px;
    padding: 16px;
    min-height: 154px;
    display: flex;
    flex-direction: column;

    &.type-materials {
      .personal-office__tasks-die {
        background-color: #D1F6A1;
      }
    }

    &.type-test {
      .personal-office__tasks-die {
        background-color: #D1F6A1;
      }
    }

    &.type-task {
      .personal-office__tasks-die {
        background-color: #7B61FF;
        p{
          color: #fff;
        }
      }
    }

    &.status-blocked {
      //pointer-events: none;
      .personal-office__tasks-die, .personal-office__tasks-question, .personal-office__tasks-bottom {
        opacity: .2;
      }
    }
  }
  &__tasks_body{
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }

  &__tasks-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__tasks-die {
    border-radius: 4px;
    padding: 2px 6px;

    p {
      font-size: 10px;
      line-height: 14px;
      font-family: $ff-medium;
      color: #1E1B1B;
    }
  }

  &__tasks-top-icon {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__tasks-question {
    margin-top: 8px;
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
    color: #FFFFFF;
    width: 100%;
    max-width: 183px;
    margin-bottom: 10px;

    a {
      color: #A3D562;
    }
  }

  &__tasks-bottom {
    margin-top: auto;
    display: flex;
    align-items: center;
  }

  &__tasks-body-item {
    display: flex;
    align-items: center;

    &:not(:last-child) {
      margin-right: 12px;
    }

    p {
      color: #FFFFFF;
      font-size: 14px;
      line-height: 18px;
      font-family: $ff-bold;
      margin-right: 6px;
    }
  }

  //  

  &__block {
    background-color: #313137;
    border-radius: 12px;
    padding: 20px 20px 22px;

    &.margin {
      margin-top: 16px;
    }
  }

  &__tasks-progress-wr {
    margin-top: 14px;
  }

  &__tasks-progress-value {
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
    color: #fff;
  }

  &__tasks-progress-line {
    position: relative;
    overflow: hidden;
    margin-top: 4px;
    width: 100%;
    height: 8px;
    background: #2B3F10;
    border-radius: 24px;
  }

  &__tasks-progress-line-body {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    transition-duration: .5s;
    background-color: #82BF00;
  }

  &__tasks-statistics {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(3, max-content );
    grid-column-gap: 24px;
    grid-row-gap: 16px;
    @media screen and (max-width: 960px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 480px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__tasks-item {

  }

  &__tasks-item-top {
    display: flex;
    align-items: center;

    p {
      font-size: 16px;
      line-height: 24px;
      color: #FFFFFF;
      font-family: $ff-bold;
    }
  }

  &__tasks-item-icon {
    margin-right: 8px;
  }

  &__tasks-key {
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
    color: #686D82;
    margin-top: 4px;
  }

  &__row-result {
    margin-top: 16px;
    display: grid;
    grid-template-columns: minmax(154px, 179px) minmax(max-content, 195px) minmax(305px, 1fr);
    grid-column-gap: 16px;
    grid-row-gap: 12px;
    @media (min-width: 815px) and (max-width: 1110px) {
      grid-template-columns: 1fr 1fr;
    }
    @media screen and (max-width: 710px) {
      grid-template-columns: 1fr 1fr;
    }
    @media screen and (max-width: 445px) {
      grid-template-columns: 1fr;
    }
  }

  &__result-item {
    display: flex;
    align-items: center;

    &:last-child {
      @media (min-width: 815px) and (max-width: 1110px) {
        grid-column: 1/ span 2;
        grid-row: 2;
      }
      @media (min-width: 445px) and (max-width: 710px) {
        grid-column: 1/ span 2;
        grid-row: 2;
      }
    }
  }

  &__result-icon {
    width: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    @media (min-width: 444px) and (max-width: 770px) {
      width: 24px;
    }
  }

  &__result-count {
    font-size: 16px;
    line-height: 24px;
    font-family: $ff-semi-bold;
    color: #fff;

    span {
      display: inline-block;
      width: max-content;
      color: #fff!important;
    }
    a{
      display: inline-block;
      width: max-content;
      color: #fff!important;
    }

    @media screen and (max-width: 480px) {
      font-size: 15px;
      letter-spacing: -0.015em;
    }
  }

  &__result-key-wr {
    display: flex;
    align-items: center;
  }

  &__result-key {
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
    color: #A2AEBF;
  }

  &__result-key-icon {
    margin-left: 4px;
  }

  &__user-rating {
    margin-top: 32px;
  }

  &__user-rating-table-container {
    &.separator {
      .personal-office__table {
        &:nth-child(3) {
          margin-bottom: 28px;

          .personal-office__table-item {
            &:nth-child(2) {
              &:after {
                content: "...";
                position: absolute;
                color: #fff;
                top: 31px;
              }
            }
          }

        }
      }

    }

    @media screen and (max-width: 695px) {
      overflow-x: auto;
      overflow-y: hidden;
    }
    @media (min-width: 816px) and (max-width: 900px) {
      overflow-x: auto;
      overflow-y: hidden;
    }
  }

  &__user-rating-table {
    margin-top: 5px;
  }

  &__user-table-wr {
    display: flex;
    align-items: center;
  }

  &__table {
    display: grid;
